"use client";

import Image from "next/image";
import { useSearchParams } from "next/navigation";
import PersonIcon from '@mui/icons-material/Person';

interface Account {
  avatar: string;
  name: string;
  visibility: number;
  accountAge: number;
}

interface AccountData {
  account: Account;
}

interface SearchResult {
  accountData: AccountData;
  accountVault: string;
}



export default function ProfilePage() {
  const searchParams = useSearchParams();
  const data = searchParams.get("data");
  if (!data) {
    return <h1>No data available or invalid search parameters.</h1>
  }

  let result: SearchResult | null = null;
  try {
    result = JSON.parse(decodeURIComponent(data));
  } catch (error) {
    console.error("Failed to parse searchParams.data", error);
    return <p>Invalid data format.</p>;
  }
  if (!result) {
    return <p>Data parsing failed. Please check your parameters.</p>;
  }
  let visibility: string = "";
  switch (result.accountData.account.visibility) {
    case 0:
      visibility = "Offline";
      break;
    case 1:
      visibility = "Online";
      break;
    case 2:
      visibility = "Busy";
      break;
    case 3:
      visibility = "Away";
      break;
    case 4:
      visibility = "Snooze";
      break;
    case 5:
      visibility = "Looking to Trade";
      break;
    case 6:
      visibility = "Looking to Play";
      break;
    default:
      visibility = "Unknown";
  }
  return (
    <div className="flex justify-center items-center h-screen bg-gray-900">
      <div className="bg-gray-800 text-white rounded-lg shadow-lg p-6 w-full max-w-md md:max-w-lg lg:max-w-2xl mx-4">
        <div className="flex flex-col md:flex-row items-center md:items-start">
          <Image
            src={result.accountData.account.avatar}
            alt="Avatar"
            width={100}
            height={100}
            className="rounded-full border-4 border-gray-700"
          />
          {/* User Info */}
          <div className="mt-4 md:mt-0 md:ml-6 text-center md:text-left">
            <h2 className="text-xl font-semibold">{result.accountData.account.name}</h2>
            <p className="text-sm text-gray-400">{visibility}</p>
            <p className="mt-2 text-gray-300">
              <PersonIcon className="text-9xl" /> <span className="font-medium">{result.accountData.account.accountAge} years</span>
            </p>
            <p className="mt-1 text-gray-300">
              Account Value: <span className="font-medium text-green-500">${result.accountVault}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
